import { Camera, CameraOptions } from "@ionic-native/camera";
// import { ImageResizer } from "@ionic-native/image-resizer";
import { ToastService } from "./toast.service";
import { Injectable } from "@angular/core";

@Injectable()
export class CameraService {
    targetWidth = 768;
    targetHeight = 768;
    options: CameraOptions = {
        quality: 100,
        allowEdit: true,
        sourceType: this.camera.PictureSourceType.CAMERA,
        destinationType: this.camera.DestinationType.DATA_URL,
        encodingType: this.camera.EncodingType.JPEG,
        mediaType: this.camera.MediaType.PICTURE
    }
    constructor(private camera: Camera, private toastSvc: ToastService) {
    }
    getPicture(witdth?: number, height?: number): Promise<string> {  
        this.options.targetWidth = witdth || this.targetWidth;
        this.options.targetHeight =  height || this.targetHeight;    
        return this.camera.getPicture(this.options).then(
            imageData => {
                // return this.imageResizer.resize({
                //     uri: imageData,
                //     quality: 100,
                //     width: 640,
                //     height: 640
                // }).then(uri => 'data:image/jpeg;base64,' + uri)
                return 'data:image/jpeg;base64,' + imageData;
            },
            err => {
                // this.toastSvc.error(err);
                return Promise.reject(err);
            }
        )
    }
}


