import {Injectable} from '@angular/core';
import {BluetoothSerial} from "@ionic-native/bluetooth-serial";
import {convertDeepLinkEntryToJsObjectString} from "@ionic/app-scripts/dist/deep-linking/util";

/**
 * Api is a generic REST Api handler. Set your API url first.
 */


@Injectable()
export class BluetoothCall {
  url: string = 'http://testdrive-dev.waservice.net:9444/api';

  isConnected: any;
  constructor(public bluetooth: BluetoothSerial) {

  this.isConnected = false
  }

  connectBT(){
    const connect = this.bluetooth.connect('DC:66:72:B1:17:0C')
    connect.subscribe(
      (next) => {
        console.log('connect next: ', next)

      },
      (error) => {
        console.log('connect error: ', error)
        this.isConnected  = false
      })

  }

  isConnectedCheck(){
    this.bluetooth.isConnected().then((suc) => {
      console.log("isConnected: ", suc)
      this.isConnected = true

    },(rej)=>{
      console.log('isConnected: ', rej)
      this.isConnected = false
    })
  }

  getListBluetoothInfo(){
    let list = [{
      deviceDisplayName: 'Genesis',
      deviceId: '00:11:67:00:54:EC'
    },{
      deviceDisplayName: 'Elantra',
      deviceId: 'DC:66:72:B1:17:0C'
    }]

    return list
  }
}



