import 'rxjs/add/operator/toPromise';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { IvtHttpService, IvtHttpClientConfig } from '@ivt/http-client';
import { UserInfoModel, UserInfoServerModel } from '../models/userInfo.model';
import { Observable } from 'rxjs/Observable';
import { Config } from '../app/app.config';
import { LocalizationModel } from '../models/config/localization.model';
@Injectable()
export class UserService extends IvtHttpService <any> {
    constructor(http: HttpClient, config: IvtHttpClientConfig) {
        super(http, '', UserInfoModel , config);
    }
    
    signup(accountInfo: any) {
        this.url = Config.api.SignUp;
        return super.search(accountInfo);
    }

    getAllLocalizationsForApp(): Observable <any>{
        this.url = Config.api.GetAllLocalizationsForApp;
        return super.search({});
    }
    getAllPermissionsForApp(): Observable <any>{
        this.url = Config.api.GetAllPermissionsForApp;
        return super.search({});
    }
}



