import { IvtHttpService, IvtHttpClientConfig } from "@ivt/http-client";
import { Config } from "../app/app.config";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Exclude } from 'class-transformer';
import { ReservationDocument } from "../models/reservation/reservation.model";
@Injectable()
export class DocumentService extends IvtHttpService<any> {
    constructor(http: HttpClient, config: IvtHttpClientConfig) {
        super(http, '' , ReservationDocument, config);
    }
    save(document: ReservationDocument){
        this.url = Config.api.CreateOrUpdateApplicationForApp;
        return super.save(document);
    }
}


