import { Injectable } from "@angular/core";
import { AlertController, AlertOptions, Alert } from "ionic-angular";
import { TranslateService } from '@ngx-translate/core';

@Injectable()
export class DialogService {

    okString: string;
    cancelString: string;

    constructor(private alertCtrl: AlertController, private translate: TranslateService) {
        this.translate.get('OK_BUTTON').subscribe((value) => {
            this.okString = value;
        });
        this.translate.get('CANCEL_BUTTON').subscribe((value) => {
            this.cancelString = value;
        });
    }
    /**
     * Wrapping of Ionic AlertController. Return a promise which will be resolved when user click OK button
     */
    alert(title?: string, subTitle?: string): Promise<any> {

        let promise = new Promise(
            (resolver, rejecter) => {
                this.alertCtrl.create({
                    // title: title,
                    subTitle: title,
                    enableBackdropDismiss: false,
                    buttons: [{
                        text: this.okString,
                        handler: () => {
                            resolver(true);
                        }
                    }]
                }).present();
            }
        );
        return promise;
    }
    createConfirm(opts?: DialogOptions): Alert {
        let alert = this.alertCtrl.create({
            title: opts ? opts.title : null,
            subTitle: opts ? opts.subTitle : null,
            enableBackdropDismiss: false,
            buttons: [
                {
                    text: this.okString,
                    handler: () => {
                        opts && opts.onConfirmed(true);
                    }
                },
                {
                    text: this.cancelString,
                    handler: () => {
                        opts && opts.onConfirmed(false);
                    }
                }
            ]
        });
        return alert;
    }


    confirm(title?: string, subTitle?: string): Promise<boolean> {

        let promise = new Promise<boolean>(
            (resolver, rejecter) => {
                this.alertCtrl.create({
                    // title: title,
                    subTitle: title,
                    enableBackdropDismiss: false,
                    buttons: [
                        {
                            text: this.okString,
                            handler: () => {
                                resolver(true);
                            }
                        },
                        {
                            text: this.cancelString,
                            handler: () => {
                                resolver(false);
                            }
                        }
                    ]
                }).present();
            }
        );
        return promise;
    }
}

export interface DialogOptions {
    title?: string;
    subTitle?: string;
    onConfirmed?: (isConfirmed: boolean) => void;
}


