import { HttpClient, HttpClientModule } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Camera } from '@ionic-native/camera';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { Geolocation } from "@ionic-native/geolocation";
import { Device } from "@ionic-native/device";
import { File } from "@ionic-native/file";
import { EmailComposer } from "@ionic-native/email-composer";
import { BluetoothSerial } from "@ionic-native/bluetooth-serial";
import { BLE } from "@ionic-native/ble";
import { DocumentViewer } from '@ionic-native/document-viewer';


import { Items } from '../mocks/providers/items';
import { VehicleItems } from '../mocks/providers/vehicleItems';
import { Settings, Api, Util } from '../providers';
import { MyApp } from './app.component';
import { SplashPage } from "../pages/splash/splash";

import { SignaturePadModule } from 'angular2-signaturepad';
// import { SignaturePadModule2 } from 'angular2-signaturepad';
import * as Sentry from 'sentry-cordova';
import { RealTimeCheckProvider } from '../providers/real-time-check/real-time-check';
import { IvtHttpClientModule, IvtHttpClientConfig } from '@ivt/http-client';
import { HttpClientConfig } from './http.client.config';
import { TestDriveProvider } from '../providers/test-drive/test-drive';
import { RealTimeItems } from "../models/realtimeItems";
import { BluetoothCall } from "../providers/bluetooth/bluetooth";
import { AppServicesModule } from '../services/services.module';

import { MomentModule } from 'ngx-moment';
import { UserService } from '../services/user.service';
import { FileTransfer } from '@ionic-native/file-transfer';
import { ImageModal } from '../pages/vehicle-health-check/imageModal/image-modal';
import { DriveShootingPage } from '../pages/drive-shooting/drive-shooting';
import { HistoryDetailPage } from '../pages/vehicle-health-check/history/vhc-history';
import { ComponentModule } from '../component/component.module';
import { TermsOfServiceModal } from '../pages/document/terms-of-service-modal/terms-of-service-modal';
import { Insomnia } from '@ionic-native/insomnia';
import { BackgroundGeolocation } from '@ionic-native/background-geolocation';
import { LocationAccuracy } from '@ionic-native/location-accuracy';
import { Diagnostic } from '@ionic-native/diagnostic';
import { PdfViewerModule } from 'ng2-pdf-viewer';
import { Autostart } from '@ionic-native/autostart';
// The translate loader needs to know where to load i18n files
// in Ionic's static asset pipeline.
export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

export function provideSettings(storage: Storage) {
  /**
   * The Settings provider takes a set of default settings for your app.
   *
   * You can add new settings options at any responseTime. Once the settings are saved,
   * these values will not overwrite the saved values (this can be done manually if desired).
   */
  return new Settings(storage, {
    option1: true,
    option2: 'Ionitron J. Framework',
    option3: '3',
    option4: 'Hello',
    option5: 'password'
  });
}

export class SentryIonicErrorHandler extends IonicErrorHandler {
  // constructor(
  //   private toastSvc: ToastService
  // ) {
  //   super();
  // }
  handleError(error) {
    console.error('[Global Error Handler] error: ', error);
    // super.handleError(error);
    try {
      Sentry.captureException(error.originalError || error);
    } catch (e) {
      console.error(e);
    }
  }
}

@NgModule({
  declarations: [
    MyApp,
    SplashPage,
    HistoryDetailPage,
    ImageModal,
    DriveShootingPage,
    TermsOfServiceModal
  ],
  imports: [
    MomentModule,
    SignaturePadModule,
    BrowserModule,
    HttpClientModule,
    AppServicesModule,
    PdfViewerModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    SplashPage,
    HistoryDetailPage,
    ImageModal,
    DriveShootingPage,
    TermsOfServiceModal
  ],
  providers: [
    BluetoothCall,
    TestDriveProvider,
    BLE,
    EmailComposer,
    File,
    FileTransfer,
    Device,
    Geolocation,
    BackgroundGeolocation,
    LocationAccuracy,
    Diagnostic,
    DocumentViewer,
    Insomnia,
    Api,
    Items,
    VehicleItems,
    Util,
    Camera,
    Autostart,
    DocumentViewer,
    SplashScreen,
    StatusBar, BluetoothSerial,
    { provide: Settings, useFactory: provideSettings, deps: [Storage] },
    // Keep this to enable Ionic's runtime error handling during development
    // { provide: ErrorHandler, useClass: IonicErrorHandler }
    { provide: ErrorHandler, useClass: SentryIonicErrorHandler },
    RealTimeCheckProvider,
    TestDriveProvider
  ]
})
export class AppModule { }



