import { Injectable } from "@angular/core";
import { BLE } from "@ionic-native/ble";
import { Platform } from "ionic-angular";
import { BluetoothSerial } from "@ionic-native/bluetooth-serial";
import { ToastService } from "./toast.service";
import { DialogService } from "./dialog.service";
import { IvtDevice } from "../models/device.model";
import { TranslateService } from '@ngx-translate/core';
import { ISubscription } from "rxjs/Subscription";

@Injectable()
export class BluetoothService {

    private subscription: ISubscription;

    constructor(
        private bluetoothLE: BLE,
        private platform: Platform,
        private bluetoothSerial: BluetoothSerial,
        private dialogService: DialogService,
        private translate: TranslateService
    ) {
    }

    private IamReady() {
        return this.platform.ready();
    }
    async isEnabled(): Promise<boolean> {
        await this.IamReady();
        try {
            await this.bluetoothSerial.isEnabled();
            return true;
        } catch (err) {
            console.error(err);
            return false;
        }
    }

    async enable(): Promise<any> {
        await this.IamReady();
        return this.bluetoothSerial.enable();
    }
    async scan() {
        await this.IamReady();
        return this.bluetoothLE.startScan([]).subscribe(
            _res => {
                console.log('bluetooth:...', _res);
            }
        );
    }

    /**
     * Promise resolve if connection is successful, reject if it fails
     * @param macAddress_or_uuid 
     */
    async connect(macAddress_or_uuid: string): Promise<any> {
        if (this.subscription) {
            // not sure if plugin auto handle unsubscribe for us
            // but if we unsubscribe, that also means we disconnect the current device
            this.subscription.unsubscribe(); 
        }
        const promise = new Promise(
            (resolve, reject) => {
                this.subscription = this.bluetoothSerial.connect(macAddress_or_uuid).subscribe(
                    _ => {
                        console.log('Pair device successfully..', macAddress_or_uuid);
                        resolve();
                    },
                    err => {
                        console.error('Pair device failed..', err);
                        reject(err);
                    }
                );
            }
        )
        return promise;
    }

    async disconnect(): Promise<any> {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
        return this.bluetoothSerial.disconnect();
    }
    async discoverUnpaired(): Promise<Array<IvtDevice>> {
        await this.IamReady();
        let isEnabled = await this.isEnabled();
        if (!isEnabled) {
            await this.enable();
        }
        return this.bluetoothSerial.discoverUnpaired().then(
            (_res: Array<IvtDevice>) => {
                // remove duplicate device
                _res = _res.filter(
                    (value, index, array) => {
                        return array.filter((v, i) => (i < index) && (v.id === value.id)).length === 0
                    }
                );
                console.log('bluetooth:...', _res);
                return _res;
            }
        );
    }
}


